"use scrict";
import Search from "@/components/Search/index.vue";
import MyCheckbox from "@/components/MyCheckbox/index.vue";
import MyFooter from "@/components/MyFooter/index.vue";
import {SEND_NOTICE} from "@/config/router.js";

export default {
	data() {
		return {
			type: "",
			value: [],
			list: []
		}
	},
	computed: {
		bindValue: {
			get() {
				return this.value;
			},
			set(value) {
				console.log(value);
				this.value = value;
			}
		}
	},
	onLoad(options) {
		this.type = options.type;
		this.$post(this.$api.GET_DEPARTMENT_LIST, {
			departmentType: this.type,
			bizType: "NOTICE"
		}).then(res => {
			this.list = res.data;
		});
	},
	methods: {
		loadContacts: async function ({item} = {}) {
			return this.$post(this.$api.GET_CONTACTS_LIST, {
				userType: this.type,
				departmentId: item.id
			}).then(res => {
				return res.data;
			});
		},
		submit: function () {
			this.$eventBus.$emit("setNoticeReceive", this.value);
			this.$toPage(SEND_NOTICE);
		}
	},
	components: {
		Search,
		MyCheckbox,
		MyFooter
	}
}
